.App {
  color: white;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center !important;
  background: linear-gradient(
    to top left,
    rgb(0, 13, 43),
    rgb(22, 50, 104)
  ) !important;
}

.App-LandingPage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;

  font-size: 1rem;
  width: auto;
}

.App-SideBarSearchContainer {
  width: 20vw;
  height: 84vh;
  margin-right: 3vw;
  padding-top: 0vh;
  margin-bottom: 0;
  padding-bottom: 0;
}

.App-GamesContainer {
  color: white;

  height: 100vh;

  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center !important;
}

.App-LogoContainer {
  height: 90vh;
  margin-left: 3vw;
  width: 20vw;
  font-size: 1vw;

  margin-bottom: 0;
  padding-bottom: 0;
}

.App-GamesContainer h3 {
  margin: 0;
  padding: 0;
}

.game-container {
  display: flex;
  width: 60vw;
  height: 60vh;
}

.GameContainer-ActiveImageContainer {
  display: flex;
  justify-content: center;

  width: 18vw;
  height: 48vh;
}

.text {
  font-size: 2vh;
  width: 30vw;
  margin: 0;

  padding-left: 4em;
}

.text h1 {
  margin: 0 auto;

  padding: 0;
}

img {
  width: 100%;
}

.profileImage {
  width: 10vw;
  height: 10vh;
}

.ImageContainer {
  display: flex;
  border: solid red;

  height: 50vh;
  justify-content: center;
}

input {
  background: rgba(255, 255, 255, 0.26);

  height: 4vh;
  color: white;
  width: 100%;
  text-indent: 20px;
  border-radius: 20px 20px 20px;
  border: none;
  outline: none;
}

input::-webkit-input-placeholder {
  color: white;
}

input::-ms-input-placeholder {
  color: white;
}

input::placeholder {
  color: white;
}

input::-ms-clear {
  display: none;
}

button {
  background: transparent;
  position: absolute;
  top: 25%;
  right: 5%;
  cursor: pointer;
  outline: none;
  color: white;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: white;
}

.Search {
  position: relative;
  width: 100%;
}

.GameSuggestion {
  width: 6vw !important;
  height: 12vh !important;
}

.SuggestionsContainer-ActiveSuggestionImageContainer {
  display: flex;
  justify-content: center;
  cursor: pointer;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  width: 6vw;
  height: 20vh;
}

.SuggestionsContainer-SuggestionImageContainer {
  display: flex;
  -webkit-filter: brightness(50%);
          filter: brightness(50%);
  justify-content: center;
  transition: -webkit-transform 0.2s, -webkit-filter 0.3s;
  transition: transform 0.2s, filter 0.3s;
  transition: transform 0.2s, filter 0.3s, -webkit-transform 0.2s, -webkit-filter 0.3s;

  height: 20vh;
}

.SuggestionsContainer-SuggestionImageContainer:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  -webkit-filter: brightness(100%);
          filter: brightness(100%);
  cursor: pointer;
}

ul {
  display: flex;
  justify-content: space-between;

  align-items: center;
  list-style: none;
  padding: 0;
}

li {
  padding: 10px;

  margin-left: 0;
}

html,
body {
  margin: 0px;
  padding: 0px;
  font-family: "Lato", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(
    to top,
    rgb(37, 42, 62),
    (60, 81, 113)
  ) !important;
}

h1,
h2,
h3 {
  font-weight: 400;
}

p {
  font-weight: 300;
  color: rgb(226, 226, 226);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

